"use client";

import { useForm } from "react-hook-form";
import { z } from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { useRouter } from "next/navigation";

import {
  Form,
  FormField,
  FormItem,
  FormLabel,
  FormControl,
  FormMessage,
} from "@/components/ui/form";

import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Loader2, AlertTriangle } from "lucide-react";
import { toast, Toaster } from "sonner";
import { handleResetPassword } from "@/redux/slices/auth/resetPassword";


const formSchema = z
  .object({
    newPassword: z.string().min(6, "كلمة المرور يجب أن تكون 6 أحرف على الأقل"),
    confirmPassword: z.string().min(6, "يرجى تأكيد كلمة المرور"),
  })
  .refine((data) => data.newPassword === data.confirmPassword, {
    message: "كلمتا المرور غير متطابقتين",
    path: ["confirmPassword"],
  });

export default function ResetPassword() {
  const dispatch = useDispatch();
  const router = useRouter();

  const { success, loading, error } = useSelector((state) => state.resetPassword);

  const form = useForm({
    resolver: zodResolver(formSchema),
    defaultValues: {
      newPassword: "",
      confirmPassword: "",
    },
  });

  const onSubmit = (values) => {
    dispatch(handleResetPassword(values.newPassword));
  };

  useEffect(() => {
    if (success) {
      toast.success("تم تغيير كلمة المرور بنجاح");
      setTimeout(() => {
        router.push("/auth/login");
      }, 1500);
    }
  }, [success, router]);

  useEffect(() => {
    if (error) {
      toast.error(error.message || "حدث خطأ أثناء تغيير كلمة المرور");
    }
  }, [error]);

  return (
    <div className="w-full min-h-screen bg-[#f3f4f6] flex justify-center items-center p-4">
      <Toaster />

      <div className="w-full max-w-lg bg-white rounded-3xl shadow-2xl p-10" dir="rtl">
        <h1 className="text-4xl font-bold text-[#03071e] mb-6 text-center">
          إعادة تعيين كلمة المرور
        </h1>

        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="newPassword"
              render={({ field }) => (
                <FormItem>
                  <FormLabel className="text-[#370617]">كلمة المرور الجديدة</FormLabel>
                  <FormControl>
                    <Input
                      {...field}
                      type="password"
                      placeholder="كلمة المرور الجديدة"
                      className="text-right border-[#e85d04] focus:ring-[#dc2f02]"
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="confirmPassword"
              render={({ field }) => (
                <FormItem>
                  <FormLabel className="text-[#370617]">تأكيد كلمة المرور</FormLabel>
                  <FormControl>
                    <Input
                      {...field}
                      type="password"
                      placeholder="أعد كتابة كلمة المرور"
                      className="text-right border-[#e85d04] focus:ring-[#dc2f02]"
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <Button
              type="submit"
              disabled={loading}
              className="w-full bg-[#e85d04] hover:bg-[#dc2f02] text-white text-lg py-4 rounded-xl"
            >
              {loading ? (
                <span className="flex items-center justify-center gap-2">
                  <Loader2 className="w-4 h-4 animate-spin" /> جاري تغيير كلمة المرور...
                </span>
              ) : (
                "تغيير كلمة المرور"
              )}
            </Button>
          </form>
        </Form>

        <p className="mt-4 text-sm text-center">
          هل تذكرت كلمة المرور؟{" "}
          <a href="/auth/login" className="text-[#d00000] hover:text-[#9d0208] hover:underline">
            تسجيل الدخول
          </a>
        </p>
      </div>
    </div>
  );
}
