import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

// Async thunk for resetting password
export const handleResetPassword = createAsyncThunk(
  "auth/handleResetPassword",
  async (data, thunkAPI) => {
    try {
      const req = await fetch("http://localhost:5000/api/v1/auth/reset_password", {
        method: "POST",
        credentials: "include",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });

      const res = await req.json();

      if (!req.ok) {
        return thunkAPI.rejectWithValue(res); // استرجاع رسالة الخطأ من السيرفر
      }

      console.log(res);
      return res;
    } catch (error) {
      console.error(error);
      return thunkAPI.rejectWithValue({ message: "Network error" });
    }
  }
);

// الحالة الابتدائية
const initialState = {
  successMessage: null,
  loading: false,
  error: null,
};

// Slice
const ResetPasswordSlice = createSlice({
  name: "resetPassword",
  initialState,
  extraReducers: (builder) => {
    builder
      .addCase(handleResetPassword.pending, (state) => {
        state.loading = true;
        state.error = null;
        state.successMessage = null;
      })
      .addCase(handleResetPassword.fulfilled, (state, action) => {
        state.loading = false;
        state.successMessage = action.payload.message || "Password reset successful.";
      })
      .addCase(handleResetPassword.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload.message || "Failed to reset password.";
      });
  },
});

export default ResetPasswordSlice.reducer;
